@use 'base/variables' as v;
@use 'base/mixins' as m;

.favorite {
    background-color: v.$white;
    padding: 4rem;
    margin-bottom: 2rem;
    box-shadow: 0px 0px 2.6rem -.8rem darken(v.$lightGrey, 30%);
    border-radius: 1rem;

    &:last-of-type {
        margin-bottom: 0;
    }

    @include m.tablet {
        margin-bottom: 0;
    }

    &__grid {
        @include m.tablet {
            display: grid;
            grid-template-columns: 2fr 3fr;
            gap: 2rem;
            align-items: center;
        }

        @include m.desktop {
            grid-template-columns: 1fr 3fr;
            gap: 4rem;
        }
    }

    &__img {
        max-width: 20rem;
        margin: 0 auto 2rem auto;
    }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            align-items: flex-start;
        }
    }

    &__name {
        margin: 1rem 0;
        color: v.$primary;
        font-size: 2.4rem;
    }

    &__description {
        margin: .5rem 0;
        text-align: center;
        font-size: 1.6rem;
        color: v.$secondary;
        line-height: 1.7;

        @include m.tablet {
            text-align: left
        }
    }
}